export const initialState = state => ({
    state,
    type: 'INITIAL_STATE',
});

export const setErr = error => ({
    type: 'SET_ERROR',
    payload: error,
});

export const setName = name => ({
    type: 'SET_NAME',
    payload: name,
});

export const displayName = name => ({
    type: 'DISPLAY_NAME',
    payload: name,
});
