import React from 'react';
import { connect } from 'react-redux';

import './style.css';
import { displayName, setName, setErr } from '../../actions';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.submitName = this.submitName.bind(this);
    }

    handleChange(event) {
        this.props.setName(event.target.value);
    }

    submitName() {
        if (this.props.name.length >= 1) {
            this.props.setErr(false);
            this.props.displayName(this.props.name);
        }
        else this.props.setErr(true);
    }

    render() {
        return (
          <div className="presentation">
            <h1>Bingo</h1>
            <input
              type="text"
              placeholder="Enter your name"
              value={this.props.name}
              onChange={this.handleChange}
            />
          </div>
        );
    }

}

function mapDispatchToProps(dispatch) {
    return ({
        displayName: (name) => dispatch(displayName(name)),
        setName: (name) => dispatch(setName(name)),
        setErr: (error) => dispatch(setErr(error)),
    });
}

function mapStateToProps(props) {
    return ({
        name: props.home.name,
        showErr: props.home.showErr
    });
}

export default connect(mapStateToProps, mapDispatchToProps)(Home); 