const presentation = (state = 
    {
        name: '',
        showErr: false,
    }, action) => {
        
    switch (action.type) {
        case 'INITIAL_STATE': {
            return state;
        }

        case 'SET_NAME': {
            return { ...state, name: action.payload };
        }

        case 'SET_ERROR': {
            return { ...state, showErr: action.payload };
        }

        case 'DISPLAY_NAME': {
            return { ...state, name: action.payload };
        }

        default: return state;
    }
};

export default presentation;
